<template>
  <NavBar />
  <div class="min-h-screen flex bg-gray-50 py-4 px-4 sm:px-6 lg:px-8">
    <div class="grid grid-flow-row auto-rows-max">
      <div><h1 @click="gotoAbout()">หน้าหลัก</h1></div>
      <div>
        <h2>Count: {{ $store.getters["counter/GET_COUNT"] }}</h2>
      </div>
      <div>
        {{ humidityBranch1 }}
        {{ humidityBranch2 }}
        {{ pmBranch1 }}
        {{ pmBranch2 }}
        {{ temperatureBranch1 }}
        {{ temperatureBranch2 }}
      </div>
      <button
        @click="increaseCount"
        class="bg-blue-400 w-full flex justify-center py-2 px-4 text-sm font-medium rounded-md text-white"
      >
        Increase +
      </button>
      <button
        @click="decreaseCount"
        class="bg-red-400 w-full flex justify-center py-2 px-4 text-sm font-medium rounded-md text-white"
      >
        Decrease -
      </button>
    </div>
  </div>
</template>

<script>
import router from "@/router";
import store from "@/store";
import NavBar from "@/components/NavBar";
import { ref, onMounted, onUnmounted } from "vue";
import UserProvider from "@/resources/user_provider";
import firebase from "firebase/app";
import "firebase/database"; // If using Firebase database
import "firebase/storage"; // If using Firebase storage

const firebaseConfig = {
  apiKey: "AIzaSyBxhuIoX-oA8Nh803UgEMBd79hzH8Cj5hM",
  authDomain: "drugstore2328245.firebaseapp.com",
  databaseURL: "https://drugstore2328245.firebaseio.com",
  projectId: "drugstore2328245",
  storageBucket: "drugstore2328245.appspot.com",
  messagingSenderId: "616672198177",
  appId: "1:616672198177:web:cdc5bc87559d5ba303ad60",
  measurementId: "G-WRYZ030G0W",
};

const firebaseApp = firebase.initializeApp(firebaseConfig);

export default {
  name: "Home",
  components: { NavBar },

  setup() {
    let h1 = firebaseApp.database().ref("Humidity");
    let h2 = firebaseApp.database().ref("Humidity_b2");
    let p1 = firebaseApp.database().ref("PM25");
    let p2 = firebaseApp.database().ref("PM25_b2");
    let t1 = firebaseApp.database().ref("Temperature");
    let t2 = firebaseApp.database().ref("Temperature_b2");

    const humidityBranch1 = ref("");
    const humidityBranch2 = ref("");
    const pmBranch1 = ref("");
    const pmBranch2 = ref("");
    const temperatureBranch1 = ref("");
    const temperatureBranch2 = ref("");

    onMounted(() => {
      console.log("mounted1");
      getUsers();

      h1.on("value", (ss) => {
        humidityBranch1.value = ss.val();
      });
      h2.on("value", (ss) => {
        humidityBranch2.value = ss.val();
      });
      p1.on("value", (ss) => {
        pmBranch1.value = ss.val();
      });
      p2.on("value", (ss) => {
        pmBranch2.value = ss.val();
      });
      t1.on("value", (ss) => {
        temperatureBranch1.value = ss.val();
      });
      t2.on("value", (ss) => {
        temperatureBranch2.value = ss.val();
      });
    });

    onUnmounted(() => {
      h1.off();
      h2.off();
      p1.off();
      p2.off();
      t1.off();
      t2.off();
    });

    const getUsers = async () => {
      const userService = new UserProvider();
      const data = await userService.getUser();
      console.log(data);
    };

    const gotoAbout = () => {
      router.push("/about");
    };

    const increaseCount = () => {
      store.dispatch("counter/ACTION_INCREASE_COUNT");
    };

    const decreaseCount = () => {
      store.dispatch("counter/ACTION_DECREASE_COUNT");
    };

    return {
      gotoAbout,
      decreaseCount,
      increaseCount,
      humidityBranch1,
      humidityBranch2,
      pmBranch1,
      pmBranch2,
      temperatureBranch1,
      temperatureBranch2,
    };
  },
};
</script>
